    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 1000;
    height: 100%;
    background-color: black;
    opacity: .92;
    border: var(--primary-color) 2px solid;
    border-right: none;
    border-radius: 8px 0 0 8px;
    color: var(--primary-color);
    margin-top: 50px;
    transition: .6s;
    width: 60%;
}

.nav-mobile-wrapper-desactive{
    height: 100%;
    position: fixed;
    bottom: 0;
    right: 0;
    width: 0;
    border-radius: 8px 0 0 8px;
    border: var(--primary-color) 2px solid;
    visibility: hidden;
    transition: .6s;
    right: 0;

}



